import React, { useState, useEffect, useRef } from "react"
import { SwitchTransition, CSSTransition } from "react-transition-group"
import Header from "./Header"
import Button from "./Button"
import { Form } from 'react-final-form'
import {
  disableBodyScroll,
  enableBodyScroll,
  clearAllBodyScrollLocks,
} from "body-scroll-lock"
import { isApple } from "../lib/utils"

const Layout = ({ children }) => {
  const [state, setState] = useState(0)
  const layout = useRef()

  const countOfSteps = children.length
  const percent = children[state].props.percent

  const [windowInnerHeight, setWindowInnerHeight] = useState(null)
  const windowInnerHeightRef = useRef(windowInnerHeight)

  const scrollElementIntoView = element => {
    // alert(element.getBoundingClientRect().height)

    if (true
      // (windowInnerHeightRef.current -
      //   element.getBoundingClientRect().height -
      //   document.querySelector("header").getBoundingClientRect().height) /
      // 3 <
      // element.getBoundingClientRect().top
    ) {
      // element.value = element.getBoundingClientRect().top
      document.getElementById(`scroll-${element.name}`).scrollIntoView()
    }

  }

  const nextStep = () => {
    if (state < countOfSteps - 1) {
      setState(state => ++state)
        // document.querySelector("#hack").focus();
      // }

    }

    return;
  }

  const previousStep = () => {
    if (state > 0) {
     
      setState(state => --state);

      // document.querySelector("header").scrollIntoView();
      // if(document.querySelector(".input"))
      //   document.querySelector(".input").focus();
    }

  }

  useEffect(() => {
    const childPath = children[state].props.path

    if (!window.history.state) {
      window.history.pushState({ path: childPath }, "/", `/${childPath}`)

      return
    }
    if (window.history.state.path !== childPath) {
      window.history.pushState({ path: childPath }, "/", `/${childPath}`)
    }
  }, [state])

  useEffect(() => {
    setWindowInnerHeight(window.innerHeight)
    windowInnerHeightRef.current = window.innerHeight

    document.addEventListener("click", e => {
      if (isApple()) {
        if (e.target.tagName.toUpperCase() === "INPUT") {
          document.querySelector(".test").style.display = "block"
          document.getElementById("hack").focus()

          let timer = setTimeout(() => {
            scrollElementIntoView(e.target)

            e.target.focus()
          }, 50)

          e.target.addEventListener("focusout", a => {
            if (document.querySelector(".test") && !a.relatedTarget) {
              document.querySelector(".test").style.display = "none"
            }
          })

          return () => {
            clearTimeout(timer)
          }
        }
        else {
          if (document.querySelector(".test")) {
            document.querySelector(".test").style.display = "none"
          }
        }
      }



    })

    document.addEventListener("focusin", e => {
      if (isApple()) {
        if (e.target.tagName.toUpperCase() === "INPUT") {
          if (document.querySelector(".test")) {
            document.querySelector(".test").style.display = "block"
          }
        }
      }
    })


  }, [])

  useEffect(() => {
    function setDocHeight() {
      document.documentElement.style.setProperty(
        "--vh",
        `${window.innerHeight / 100}px`
      )
    }

    document.addEventListener("keydown", e => {


      if (e.keyCode == 13) {
        e.preventDefault();
        let flagPreventNext = false;

        if(isApple()){
          let inputs = document.querySelectorAll('.input');
          if (inputs) {
            Array.from(inputs).some(input => {
              if (!input.value) {
                scrollElementIntoView(input);
  
                let timer = setTimeout(() => {
                  input.focus();
                }, 50)
                flagPreventNext = true;
                return true;
              }
            })
          }
        }


        if (!flagPreventNext) {
          nextStep();
        }



      }
    })

    setDocHeight()
    disableBodyScroll(document.querySelector(".main"));

  }, [])


  return (
    <>
      <div className="layout" ref={layout}>
        <Header handlePreviousStep={() => previousStep()} percent={percent} />
        <main className="main">
          <SwitchTransition>
            <CSSTransition
              key={state}
              addEndListener={(node, done) =>
                node.addEventListener("transitionend", done, false)
              }
              classNames="fade"
            >
              <Form
                onSubmit={() => { }}
                render={({ handleSubmit, invalid, pristine, values }) => (
                  <>
                    <form className={`form_container-${children[state].props.path}`}>


                      {children[state]}

                      <div
                        className="container-button"
                        onClick={() => {
                          nextStep()
                        }}
                      >
                        <Button classList="button--continue" disabled={invalid} > {children[state].props.customButton || "Przejd Dalej "} </Button>
                      </div>
                      <div className="test"></div>

                    </form>
                  </>
                )}
              />

            </CSSTransition>
          </SwitchTransition>
        </main>
      </div>
    </>
  )
}

export default Layout
