import React, { useRef } from 'react';
import { Field } from 'react-final-form';
import { isApple } from '../lib/utils';



const composeValidators = (...validators) => value =>
  validators.reduce((error, validator) => error || validator(value), undefined)

const Input = ({ validate = [], mode, name, className = "", format, label, autofocus }) => {
    const refInput = useRef();

    return (

        <Field
            name={name}
            format={format}
            validate={ composeValidators(...validate)}
            id={name}
        >
            {({ input }) => (
                <fieldset className={`fieldset ${className}`}>
                    <input
                        ref={refInput}
                        {...input}
                        className="input"
                        id={name}
                        type="text"
                        inputMode={mode}
                        required 
                        // autoFocus={autofocus ? true : false}
                        autoFocus={autofocus && !isApple() ? "autoFocus" : false}
                    />
                    <label className="moving_label"  htmlFor={name}> {label} </label>

                    <p className="virtual_paragraph_to_scroll" id={`scroll-${name}`} aria-hidden="true"> scroll </p>
                </fieldset>
            )}
        </Field>
    )
}

export default Input;